import { Component, OnInit } from '@angular/core';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { ActivatedRoute } from '@angular/router';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { NavController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth.service';

@Component({
	selector: 'app-info-add-jeu-video',
	templateUrl: './info-add-jeu-video.page.html',
	styleUrls: [ './info-add-jeu-video.page.scss' ]
})
export class InfoAddJeuVideoPage implements OnInit {
	selectedJeuVideo: JeuVideo;

	constructor(
		private navCtrl: NavController,
		private serviceJeuVideo: DataJeuVideoService,
		private activatedRoute: ActivatedRoute,
		private authService: AuthService
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idJeuVideo')) {
				this.navCtrl.navigateBack([ '/selectionner-jeu-video' ]);
				return;
			}
			this.selectedJeuVideo = { ...this.serviceJeuVideo.getInAllJeuVideo(paramMap.get('idJeuVideo')) };
		});
		this.selectedJeuVideo.note = 2;
	}
	goBack() {
		this.navCtrl.navigateBack([ '/selectionner-jeu-video' ]);
	}

	Valider() {
		this.serviceJeuVideo.addInMyJeuVideo(this.selectedJeuVideo, this.authService.userId);
		this.navCtrl.navigateBack([ '/medias/jeux-video' ]);
	}

	passerNote(note: number) {
		this.selectedJeuVideo.note = note;
	}
}
